export interface DialogData {
  animal: string;
  name: string;
};
export interface Subject {
  name: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],

})
export class AppComponent implements OnInit {
  title = 'incDemo';
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  animal: string;
  name: string;
  durationInSeconds = 5;
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  myForm: FormGroup;

  GradeArray: any = ['8th Grade', '9th Grade', '10th Grade', '11th Grade', '12th Grade'];
  SubjectsArray: Subject[] = [];
  submitted: any;
  latitude = -28.68352;
  longitude = -147.20785;
  mapType = 'satellite';
  public loading = false;
  tableData = [];
  today: number = Date.now();
  items = [];
  pageOfItems: Array<any>;
  imageURL: any;
  imgViewPath = "file:///E:/incDemo/images/1578287882506.png";
  fruits: string[] = ['Lemon'];
  allFruits: string[] = ['Apple', 'Lemon', 'Lime', 'Orange', 'Strawberry'];
  recordNotFound: any;
  emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  addresses:any=[];




  dataSource = new MatTableDataSource();



  displayedColumns: string[] = ['Name', 'Email', 'Gender', 'Birth Date', 'Grade', 'Profile Photo', 'Action'];


  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  serializedDate = new FormControl((new Date()).toISOString());

  constructor(public dialog: MatDialog, 
              private _formBuilder: FormBuilder, 
              private _snackBar: MatSnackBar, 
              private ds: DataService,
              private permissionsService: NgxPermissionsService
              ) { }

  openDialog(empId: any): void {
    this.ds.getEmployeeDetails(empId).subscribe(data => {
      if (data['code'] == '00') {

        this.loading = false;

        const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
          width: '600px',
          data: data['result'][0]
        });

        dialogRef.afterClosed().subscribe(result => {
          console.log('The dialog was closed');
          this.animal = result;
        });
      }
    });
  }


  openDialogForUpdateRecord(empId: any): void {




    this.ds.getEmployeeDetails(empId).subscribe(data => {
      if (data['code'] == '00') {

        this.loading = false;

        const dialogRef = this.dialog.open(DialogOverUpdateRecord, {
          width: '600px',
          data: data['result'][0]
        });

        dialogRef.afterClosed().subscribe(result => {
          console.log('The dialog was closed');
          this.animal = result;
        });
      }
    });
  }



  openSnackBar(msg) {
    this._snackBar.openFromComponent(PizzaPartyComponent, {
      duration: 2 * 1000,
      verticalPosition: 'top',
      data: msg

    });
  }




  ngOnInit() {

    
    //this.permissionsService.loadPermissions(perm);

    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
    this.reactiveForm();



    this.emplyeeList();



  }
  get f() { return this.myForm.controls; }
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  emplyeeList() {
    this.loading = true;
    this.ds.getEmployeeRecord().subscribe(data => {
      if (data['code'] == '00') {

        this.dataSource.data = data['result'];
        this.loading = false;
      }
    },
    err => {
        console.log(err);
        let msg = "Sever not Responding";
        this.openSnackBar(msg);
      });
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

  }

  onChangePage(pageOfItems: Array<any>) {
    // update current page of items
    this.pageOfItems = pageOfItems;
  }
  /* Reactive form */
  reactiveForm() {
    this.myForm = this._formBuilder.group({
      name: ['', [Validators.required,Validators.minLength(6),Validators.pattern('^[a-zA-Z \-\']+')]],
      email: ['', [Validators.required,Validators.email,Validators.pattern(this.emailPattern)]],
      gender: ['Male'],
      dob: ['', [Validators.required]],
      grade: [''],
      subjects: [this.SubjectsArray],
      profileImg: ['', [Validators.required]],
      acceptTerms: [false, Validators.requiredTrue],
   
      addresses: this._formBuilder.array([ this.createAddress() ])
       // this._formBuilder.control('')
      
      

    })
  }

  createAddress(): FormGroup {
    return this._formBuilder.group({
      address: '',
      street: '',
      city: '',
      country: ''
    });
 }

 addAddress(): void {
  this.addresses = this.myForm.get('addresses') as FormArray;
  this.addresses.push(this.createAddress());
}

  get addressControls() {
    return this.myForm.get('addresses')['controls'];
  }

  deleteEmployeeRecord(empid:any)
  {
    this.ds.deleteEmployeeRecord(empid).subscribe(data => {
      if (data['code'] == '00') {
        let msg = "Remove Successfully";
        this.openSnackBar(msg);
        this.emplyeeList();
      }
      else {
        alert('went wrong');
      }
    });

  }
  onFileChange(event) {
    const file = (event.target as HTMLInputElement).files[0];
    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.imageURL = reader.result;
    }
    reader.readAsDataURL(file);
  }
 /* Date */
  date(e) {
    var convertDate = new Date(e.target.value).toISOString().substring(0, 10);
    this.myForm.get('dob').setValue(convertDate, {
      onlyself: true
    })
  }

  submitForm() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.myForm.invalid) {
            let msg = "Invalid Data";
            this.openSnackBar(msg);
            return;
    }
    else {
    
      

      this.ds.saveEmployee(this.myForm.value, this.imageURL).subscribe(data => {
        if (data['code'] == '00') {
          let msg = "Save successfully";
          this.openSnackBar(msg);
          this.emplyeeList();
        }
        else {
          alert('went wrong');
        }
      });

    }


  }



  /* Add dynamic languages */
  add(event: any): void {
    const input = event.input;
    const value = event.value;
    // Add language
    if ((value || '').trim() && this.SubjectsArray.length < 5) {
      this.SubjectsArray.push({ name: value.trim() })
    }
    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  /* Remove dynamic languages */
  remove(subject: Subject): void {
    const index = this.SubjectsArray.indexOf(subject);
    if (index >= 0) {
      this.SubjectsArray.splice(index, 1);
    }
  }

  /* Handle form errors in Angular 8 */
  public errorHandling = (control: string, error: string) => {
    return this.myForm.controls[control].hasError(error);
  }


}

