@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: 'dialog-overview-update-record.html',
})
export class DialogOverUpdateRecord implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private _formBuilder: FormBuilder,
    private datePipe: DatePipe,
    private ds: DataService, private _snackBar: MatSnackBar) { }

  displayedColumns: string[] = ['Name', 'Value'];
  updateForm: FormGroup;
  imageURL: any;
  GradeArray: any = ['8th Grade', '9th Grade', '10th Grade', '11th Grade', '12th Grade'];
  SubjectsArray: Subject[] = [];
  submitted: any;
  selectedId: any;
  selectedHobbies: [string];
  myhobbies: any = [
    {
      name: "Sports",
      value: "sports"
    },
    {
      name: "Music",
      value: "music"
    },
    {
      name: "Movie",
      value: "movie"
    },
    {
      name: "Reading",
      value: "reading"
    },
    {
      name: "Writing",
      value: "writing"
    }
  ];

  ordersData:any = [
    { id: 1, name: 'sports' },
    { id: 2, name: 'music' },
    { id: 3, name: 'movie' },
    { id: 4, name: 'reading' }
  ];
  addresses:any=[];
  

  myselectedhobbies:any=['sports','music'];
  

  ngOnInit() {






    this.updateForm = this._formBuilder.group({

      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      gender: ['Male'],
      dob: ['', [Validators.required]],
      grade: [''],
      subjects: [this.SubjectsArray],
      profileImg: ['', [Validators.required]],
      orders:new FormArray([]),
      addresses: this._formBuilder.array([ this.createAddress() ])

    });


    

    this.addCheckboxes();
   
    this.selectedId = this.data['_id'];





    //this.datePipe.transform(this.data['employeeBirthDate'],'MM/dd/yyyy')
    this.updateForm.patchValue({

      name: [this.data['employeeName']],
      email: [this.data['employeeEmail']],
      gender: [this.data['employeeGender']],
      dob: new Date(this.data['employeeBirthDate']).toISOString(),
      grade: this.data['grade'],
      profileImg: "",
      orders:[],
      addresses:[this.createAddress()]
    });

  }
  createAddress(): FormGroup {
    return this._formBuilder.group({
      address: '',
      street: '',
      city: '',
      country: ''
    });
 }

 addAddress(): void {
  this.addresses = this.updateForm.get('addresses') as FormArray;
  this.addresses.push(this.createAddress());
}

private updateAddress() {
    this.addresses.forEach((o, i) => {
      let result=this.myselectedhobbies.includes(o.name);
      
      const control = new FormControl(result); // if first item set to true, else false
      (this.updateForm.controls.orders as FormArray).push(control);
    });
}

  get addressControls() {

    return this.updateForm.get('addresses')['controls'];
  }

  private addCheckboxes() {
    this.ordersData.forEach((o, i) => {
      let result=this.myselectedhobbies.includes(o.name);
      
      const control = new FormControl(result); // if first item set to true, else false
      (this.updateForm.controls.orders as FormArray).push(control);
    });
  }



  onNoClick(): void {
    console.log("@@@@@@@@@@@@@@");
    this.dialogRef.close();
  }

  onFileChange(event) {
    const file = (event.target).files[0];
    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.imageURL = reader.result;
    }
    reader.readAsDataURL(file);
  }

 

  getSelectedHobbies() {
    const selectedOrderIds = this.updateForm.value.orders
      .map((v, i) => v ? this.ordersData[i].id : null)
      .filter(v => v !== null);
      console.log(selectedOrderIds);
  }

  

  submitUpdateForm() {

    this.submitted = true;
    // stop here if form is invalid
    if (this.updateForm.invalid) {
      let msg = "Invalid Data";
      this.openSnackBar(msg);
      return;
    }
    else {
      this.ds.updateEmployee(this.updateForm.value, this.imageURL, this.selectedId).subscribe(data => {
          if (data['code'] == '00') {
            let msg = "Save successfully";
            this.openSnackBar(msg);

          }
          else {
            alert('went wrong');
          }
        });

      }


  }

  openSnackBar(msg) {
    this._snackBar.openFromComponent(PizzaPartyComponent, {
      duration: 2 * 1000,
      verticalPosition: 'top',
      data: msg
    });
  }



}


