import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor(private http: HttpClient) { }

  localUrl = "http://localhost:6787/api/employee/addEmployee";


  saveEmployee(data: any, imageURL: any): Observable<any[]> {
    //employeeName/employeeEmail/employeeGender/grade/subject
    const postData = {
      employeeName: data['name'],
      employeeEmail: data['email'],
      employeeGender: data['gender'],
      grade: data['grade'],
      employeeBirthDate: data['dob'],
      subject: JSON.stringify(data['subjects']),
      profileImg: imageURL,
      addresses: data['addresses']
    }

    return <Observable<any[]>>this.http.post(this.localUrl, postData).pipe(

      retry(1),

      catchError(this.handleError)

    );
  }


  getEmployeeRecord(): Observable<any[]> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'auth': 'aaaaaaaaaaaaaabbbbbbbbbbbbbbbbbbbccccccccccccccccc'
      })
    };

    return <Observable<any[]>>this.http.get("http://localhost:6787/api/employee/employeeList", httpOptions).pipe(

      retry(1),

      catchError(this.handleError)

    );
  }

  getEmployeeDetails(empId: any): Observable<any[]> {

    return <Observable<any[]>>this.http.get("http://localhost:6787/api/employee/employeeDetails/" + empId).pipe(
      retry(1),
      catchError(this.handleError)

    );
  }

  getProductList(): Observable<any[]> {
    return <Observable<any[]>>this.http.get("http://localhost:6787/api/product/productList").pipe(
      retry(1),
      catchError(this.handleError)

    );
  }




  updateEmployee(data: any, imageURL: any, selectedId: any): Observable<any[]> {
    //employeeName/employeeEmail/employeeGender/grade/subject
    var imageArr: any[];
    imageArr.push(imageURL);



    const postData = {
      employeeName: data['name'],
      employeeEmail: data['email'],
      employeeGender: data['gender'],
      grade: data['grade'],
      employeeBirthDate: data['dob'],
      subject: JSON.stringify(data['subjects']),
      profileImg: imageArr,

    }

    return <Observable<any[]>>this.http.post(this.localUrl, postData).pipe(

      retry(1),

      catchError(this.handleError)

    );
  }

  deleteEmployeeRecord(empId: any): Observable<any[]> {


    return <Observable<any[]>>this.http.get("http://localhost:6787/api/employee/removeEmployee/" + empId).pipe(
      retry(1),
      catchError(this.handleError)

    );

  }






  handleError(error) {

    let errorMessage = '';

    if (error.error instanceof ErrorEvent) {

      // client-side error

      errorMessage = `Error: ${error.error.message}`;

    } else {

      // server-side error

      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;

    }



    return throwError(errorMessage);

  }



}
